<template>
  <Loading :active="isLoading"></Loading>
  <div>
    <div class="container">
      <div class="text-end mt-4">
        <button
          class="btn btn-primary"
          type="button"
          @click="openModal('add', article)"
        >
          
        </button>
      </div>
      <table class="table mt-4">
        <thead>
          <tr>
            <th style="width: 200px"></th>
            <th style="width: 200px"></th>
            <th></th>
            <th style="width: 100px"></th>
            <th style="width: 100px"></th>
            <th style="width: 120px"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="article in articles" :key="article.id">
            <td>{{ article.title }}</td>
            <td>{{ article.author }}</td>
            <td>{{ article.description }}</td>
            <td>{{ getDate(article.create_at) }}</td>
            <td>
              <span v-if="article.isPublic"></span>
              <span v-else></span>
            </td>
            <td>
              <div class="btn-group">
                <button
                  class="btn btn-outline-primary btn-sm"
                  type="button"
                  @click="getArticle(article)"
                >
                  
                </button>
                <button
                  class="btn btn-outline-danger btn-sm"
                  type="button"
                  @click="openModal('delete', article)"
                >
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <ArticleModal
      ref="articleModal"
      :article="tempArticle"
      :status="status"
      @update-article="getArticles"
    ></ArticleModal>

   
    <DelModal    ref="delArticleModal" :delArticle="tempArticle"  @update-article="getArticles" ></DelModal>

     <!-- <DelArticleModal
      ref="delArticleModal"
      :delArticle="tempArticle"
      @update-article="getArticles"
    ></DelArticleModal> -->

  </div>
</template>

<script>
import ArticleModal from "@/components/articleModal.vue";
import DelModal from "@/components/delarticle.vue";

export default {
  data() {
    return {
      articles: {},
      tempArticle: {},
      status: "",
      pagination: {},
      isLoading: false,
    };
  },
  components: {
    ArticleModal,
    DelModal
  },
  methods: {
    // 
    getArticles(page = 1) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/articles?page=${page}`;
      this.$http
        .get(url)
        .then((res) => {
          //  api  content  content 
          this.articles = res.data.articles;
          this.pagination = res.data.pagination;
          this.isLoading = false;
         
        })
        .catch((err) => {
        console.log(err);
        });
    },
    getDate(time) {
      //
      const date = new Date(time * 1000);
      return date.toLocaleDateString();
    },
    //api
    getArticle(article) {
      this.isLoading = true;
      const { id } = article;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${id}`;
      this.$http
        .get(url)
        .then((res) => {
          //  api  content  api  v-for  article 
          this.openModal("edit", res.data.article);
          this.isLoading = false;
        })
        .catch((err) => {
          this.$messageState(
            err.response,
            err.request,
            err.message,
            ""
          );
        });
    },
    openModal(status, item) {
      //  modal 
      this.status = status;
      if (status === "add") {
        this.tempArticle = {
          isPublic: false,
          create_at: new Date().getTime() / 1000,
          tag: [],
        };
        this.$refs.articleModal.openArticleModal(); //  modal
      } else if (status === "edit") {
        this.tempArticle = JSON.parse(JSON.stringify(item));
        this.$refs.articleModal.openArticleModal(); //  modal
      } else if (status === "delete") {
        this.tempArticle = JSON.parse(JSON.stringify(item));
        this.$refs.delArticleModal.openDeleteArticleModalModal(); //  modal
      }
    },
  },
  mounted() {
    this.getArticles();
  },
};
</script>